import { createSlice } from "redux-starter-kit";

const interval = createSlice({
  slice: "interval",
  initialState: 1,
  reducers: {
    change: (state, action) => {
      const interval = parseInt(action.payload);
      if (isNaN(interval) || interval < 1) {
        return 1;
      }

      return interval;
    },
  },
});

export const { actions, reducer } = interval;
export default reducer;
