import Block from "components/presentational/Block";
import DatePicker from "components/presentational/DatePicker";
import Lightswitch from "components/presentational/Lightswitch";
import Row from "components/presentational/Row";
import { addDays } from "date-fns";
import {
  fromUnixTimeUTC,
  getUnixTimeUTC,
  resetTimestampToDayStart,
} from "utilities/date";
import { timeIntervalSeconds } from "utilities/config";
import PropTypes from "prop-types";
import React from "react";
import { connect } from "react-redux";
import { CSSTransition } from "react-transition-group";
import { actions as allDayActions } from "reducers/allDay";
import { actions as dateActions, targets } from "reducers/dates";
import { actions as multiDayActions } from "reducers/multiDay";
import "styles/time-input-animation.scss";

@connect(
  state => ({
    allDay: state.allDay,
    multiDay: state.multiDay,
    startDate: state.dates.start,
    endDate: state.dates.end,
  }),
  dispatch => ({
    toggleAllDay: isAllDay => dispatch(allDayActions.toggle(isAllDay)),
    toggleMultiDay: isMultiDay => dispatch(multiDayActions.toggle(isMultiDay)),
    changeDate: (target, date, multiDay, allDay) =>
      dispatch(dateActions.change({ target, date: getUnixTimeUTC(date), multiDay, allDay })),
  })
)
class DateProperties extends React.Component {
  static propTypes = {
    startDate: PropTypes.number,
    endDate: PropTypes.number,
    allDay: PropTypes.bool,
    multiDay: PropTypes.bool,
    changeDate: PropTypes.func,
    toggleAllDay: PropTypes.func,
    toggleMultiDay: PropTypes.func,
  };

  render() {
    const { startDate, changeDate, endDate } = this.props;
    const { allDay, toggleAllDay } = this.props;
    const { multiDay, toggleMultiDay } = this.props;

    const endDateMinDate = getUnixTimeUTC(
      addDays(fromUnixTimeUTC(startDate), 1)
    );

    return (
      <Row>
        <Block label={"Date"} required>
          <DatePicker
            dateTimestamp={startDate}
            onChangeHandler={date => changeDate("start", date, multiDay, allDay)}
          />
          <CSSTransition
            in={!allDay}
            timeout={200}
            classNames="time-input"
            unmountOnExit
          >
            <DatePicker
              dateTimestamp={startDate}
              onChangeHandler={date => changeDate("start", date, multiDay, allDay)}
              selectTime
            />
          </CSSTransition>

          <CSSTransition
            in={!(allDay && !multiDay)}
            timeout={200}
            classNames="dash-input"
            unmountOnExit
          >
            <div>
              <div style={{ marginTop: 5, width: 14, textAlign: "center" }}>
                &ndash;
              </div>
            </div>
          </CSSTransition>

          <CSSTransition
            in={multiDay}
            timeout={200}
            classNames="date-input"
            unmountOnExit
          >
            <DatePicker
              dateTimestamp={endDate}
              onChangeHandler={date => changeDate("end", date)}
              minDateTimestamp={endDateMinDate}
            />
          </CSSTransition>

          <CSSTransition
            in={!allDay}
            timeout={200}
            classNames="time-input"
            unmountOnExit
          >
            <DatePicker
              dateTimestamp={endDate}
              minDateTimestamp={startDate + timeIntervalSeconds}
              onChangeHandler={date => changeDate("end", date)}
              selectTime
            />
          </CSSTransition>
        </Block>

        <Block label={"All Day"} width={48}>
          <Lightswitch
            name={"allDay"}
            on={allDay}
            toggleHandler={toggleAllDay}
          />
        </Block>

        <Block label={"Multi-Day"} width={64}>
          <Lightswitch on={multiDay} toggleHandler={toggleMultiDay} />
        </Block>
      </Row>
    );
  }
}

export default DateProperties;
