import { createSlice } from "redux-starter-kit";
import {
  setHours,
  setMinutes,
  setSeconds,
  setMilliseconds,
  addDays,
  addMinutes,
  isSameDay,
} from "date-fns";
import {
  getUnixTimeUTC,
  fromUnixTimeUTC,
  resetToDayEnd,
  resetTimestampToDayStart,
  resetTimestampToDayEnd,
  resetTimestampToCurrentTime,
} from "utilities/date";
import {
  timeIntervalSeconds,
  eventDuration,
  eventDurationSeconds,
} from "utilities/config";
import { actions as allDayActions } from "reducers/allDay";
import { actions as multiDayActions } from "reducers/multiDay";

export const targets = {
  start: "start",
  end: "end",
};

let date = setMilliseconds(setSeconds(setMinutes(new Date(), 0), 0), 0);
const start = getUnixTimeUTC(date);
date = resetToDayEnd(date);
const end = getUnixTimeUTC(date);

const dates = createSlice({
  slice: "dates",
  initialState: {
    start,
    end,
  },
  reducers: {
    change: (state, action) => {
      const { target, date = start, multiDay, allDay } = action.payload;
      const clone = {
        ...state,
        [target]: date,
      };

      let startDate = fromUnixTimeUTC(clone.start);
      let endDate = fromUnixTimeUTC(clone.end);

      if (target === targets.start) {
        endDate = startDate;
        if (allDay) {
          endDate = setHours(setMinutes(setSeconds(endDate, 59), 59), 23);
        } else {
          endDate = setMinutes(endDate, eventDuration);
        }

        if (multiDay) {
          endDate = addDays(endDate, 1);
        }

        clone.end = getUnixTimeUTC(endDate);
      } else if (target === targets.end) {
        if (clone.start > date) {
          clone.end = clone.start + eventDurationSeconds;
        }
      }

      return clone;
    },
  },
  extraReducers: {
    [allDayActions.toggle]: (state, action) => {
      const allDay = action.payload;

      if (allDay) {
        return {
          ...state,
          start: resetTimestampToDayStart(state.start),
          end: resetTimestampToDayEnd(state.end),
        };
      }

      const startWithCurrentTime = resetTimestampToCurrentTime(state.start);
      const endWithCurrentTime = resetTimestampToCurrentTime(state.end);

      return {
        ...state,
        start: startWithCurrentTime,
        end: endWithCurrentTime + eventDurationSeconds,
      };
    },
    [multiDayActions.toggle]: (state, action) => {
      const isMultiDay = action.payload;

      if (!isMultiDay) {
        const { start, end } = state;

        const startDate = fromUnixTimeUTC(start);
        const endDate = fromUnixTimeUTC(end);

        let resetEnd = setHours(
          setMinutes(startDate, endDate.getMinutes()),
          endDate.getHours()
        );

        const resetEndTime = resetEnd.getTime() / 1000;
        const startTime = startDate.getTime() / 1000;

        if (resetEndTime < startTime + timeIntervalSeconds) {
          resetEnd = addMinutes(startDate, eventDuration);
        }

        return { ...state, end: getUnixTimeUTC(resetEnd) };
      }

      return {
        ...state,
        end: getUnixTimeUTC(addDays(fromUnixTimeUTC(state.end), 1)),
      };
    },
  },
});

export const { actions, reducer } = dates;
export default reducer;
