import "@babel/polyfill";
import React from 'react';
import ReactDOM from 'react-dom';
import DateProperties from 'components/container/DateProperties';
import RepeatRuleProperties from 'components/container/RepeatRuleProperties';

import { Provider } from 'react-redux';
import store from './store';

class App extends React.Component {
  render() {
    return (
      <Provider store={store}>
        <DateProperties />
        <RepeatRuleProperties />
      </Provider>
    );
  }
}

const wrapper = document.getElementById('event-builder');
export const isPro = "pro" in wrapper.dataset;
export const isRepeatRulesEnabled = "repeatRulesEnabled" in wrapper.dataset;

wrapper ? ReactDOM.render(<App />, wrapper) : false;

export default App;
