import { createSlice } from "redux-starter-kit";
import { actions as datesActions, targets } from "reducers/dates";
import { resetTimestampToDayStart } from "utilities/date";

const exceptions = createSlice({
  slice: "exceptions",
  initialState: [],
  reducers: {
    add: (state, action) =>
      state.includes(action.payload)
        ? state
        : [...state, action.payload].sort((a, b) => a - b),
    remove: (state, action) => state.filter(date => date !== action.payload),
  },
  extraReducers: {
    [datesActions.change]: (state, action) => {
      let { target, date: eventStartDate } = action.payload;
      if (target !== targets.start) return state;

      eventStartDate = resetTimestampToDayStart(eventStartDate);

      return state.filter(date => date >= eventStartDate);
    },
  },
});

export const { actions, reducer } = exceptions;
export default reducer;
