import { createSlice } from "redux-starter-kit";
import { actions as dateActions, targets } from "reducers/dates";
import { isNewEvent } from "utilities/config";
import { weekDayAbbreviations } from "constants/rules";
import { getDay } from "date-fns";
import { fromUnixTimeUTC } from "utilities/date";

const byDay = createSlice({
  slice: "byDay",
  initialState: [],
  reducers: {
    change: (state, action) =>
      state.includes(action.payload)
        ? state.filter(item => item !== action.payload)
        : [...state, action.payload].sort((a, b) => a - b),
  },
  extraReducers: {
    [dateActions.change]: (state, action) => {
      const { target, date } = action.payload;
      if (!isNewEvent || target !== targets.start) return state;

      return [weekDayAbbreviations[getDay(fromUnixTimeUTC(date))]];
    },
  },
});

export const { actions, reducer } = byDay;
export default reducer;
